.certeficat-slider {
  padding: 80px 20px 40px 20px;
  overflow: hidden;
  &__inner {
    position: relative;
    padding: 0 0px;
  }

  &__controls {
    & button {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &__btn--prev {
    left: 0;
  }
  &__btn--next {
    right: 0;
  }

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    column-gap: 16px;
    row-gap: 60px;
  }

  &__slide {
    flex: 0 0 380px;
  }
}
.certeficat-slide {
  box-shadow: 4px 4px 16px 0 rgba(203, 210, 216, 0.4);
  border-radius: 40px;
  &.selected {
    border: 2px solid var(--blue-text);
  }
  &:nth-child(1) {
    background: #fff url("../img/abonement/bg1.png") no-repeat center / cover;
  }
  &:nth-child(2) {
    background: #fff url("../img/abonement/bg2.png") no-repeat center / cover;
  }
  &:nth-child(3) {
    background: #fff url("../img/abonement/bg3.png") no-repeat center / cover;
  }
  &:nth-child(4) {
    background: #fff url("../img/abonement/bg4.png") no-repeat center / cover;
  }
  &:nth-child(5) {
    background: #fff url("../img/abonement/bg5.png") no-repeat center / cover;
  }
  &:nth-child(6) {
    background: #fff url("../img/abonement/bg6.png") no-repeat center / cover;
  }
  &:nth-child(7) {
    background: #fff url("../img/abonement/bg7.png") no-repeat center / cover;
  }
  &:nth-child(8) {
    background: #fff url("../img/abonement/bg8.png") no-repeat center / cover;
  }
  &:nth-child(9) {
    background: #fff url("../img/abonement/bg9.png") no-repeat center / cover;
  }
  &__wrap {
    padding: 60px 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__pretitle {
    margin-bottom: 20px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 125%;
    letter-spacing: 0.3em;
    text-align: center;
    color: var(--dark-grey-text);
  }

  &__title {
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 100px;
    letter-spacing: 0.05em;
    line-height: 100%;
    text-transform: uppercase;
    text-align: center;
    color: var(--dark-blue-text);
  }

  &__subtitle {
    margin-bottom: 25px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 110%;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    text-align: center;
    color: var(--dark-blue-text);
  }

  &__label {
    margin-bottom: 4px;
    align-self: start;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 16px;
    line-height: 145%;
    color: #2d2d2d;
  }

  &__discount {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 125%;
    letter-spacing: 0.05em;
    text-align: center;
    color: var(--blue-text);
  }

  &__price {
    margin: 20px 0;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 110%;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    text-align: center;
    color: var(--dark-blue-text);
    &:after {
      content: " РУБ";
    }
  }
  &__link {
    padding: 16px 20px;
    display: flex;
    align-items: center;
    gap: 10px;
    border-radius: 80px;
    box-shadow: 2px 2px 8px 0 rgba(0, 17, 27, 0.4);
    background: radial-gradient(
      486.41% 219.97% at 14.73% 0%,
      #ff9d00 0%,
      #ffbf35 54.08%,
      #ffc870 100%
    );
    &:hover {
      box-shadow: 2px 2px 24px 0 rgba(0, 17, 27, 0.7);
      background: radial-gradient(
        486.41% 219.97% at 14.73% 0%,
        #ff9d00 0%,
        #ff9d00 100%
      );
      transition: 0.2s ease-in;
    }
    &:focus {
      box-shadow: 2px 2px 24px 0 rgba(0, 17, 27, 0.7);
      background: radial-gradient(
        486.41% 219.97% at 14.73% 0%,
        #f80 0%,
        #f80 100%
      );
      transition: 0.2s ease-in;
    }
    &:active {
      box-shadow: 2px 2px 24px 0 rgba(0, 17, 27, 0.7);
      background: radial-gradient(
        486.41% 219.97% at 14.73% 0%,
        #f80 0%,
        #f80 100%
      );
      transition: 0.2s ease-in;
    }
    &:disabled {
      box-shadow: 2px 2px 8px 0 rgba(0, 17, 27, 0.4);
      background: radial-gradient(
        486.41% 219.97% at 14.73% 0%,
        #ff9d00 0%,
        #ffbf35 54.08%,
        #ffc870 100%
      );
      transition: 0.2s ease-in;
    }
  }
}
.certificate {
  &__content {
    margin-bottom: 40px;
    padding: 24px;
    border-radius: 20px;
    box-shadow: 2px 3px 8px 0 rgba(221, 230, 227, 0.8);
    background: #fff;
  }

  &__content-title {
    margin-bottom: 10px;
    font-family: var(--second-family);
    font-weight: 700;
    font-size: 30px;
    line-height: 110%;
    letter-spacing: 0.04em;
    color: var(--dark-grey-text);
  }

  & ul {
    margin-bottom: 20px;
    margin-left: 24px;
  }

  & li {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    color: var(--dark-grey-text);
    &::marker {
      font-size: 24px;
      color: #73b4ff;
    }
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}

@media all and (max-width: 1000px) {
  .certeficat-slider {
    padding: 80px 20px 40px 20px;
    overflow: hidden;
    &__inner {
      position: relative;
      padding: 0 70px;
    }

    &__controls {
      & button {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }

    &__btn--prev {
      left: 0;
    }
    &__btn--next {
      right: 0;
    }

    &__wrap {
      display: flex;
      flex-wrap: nowrap;
      column-gap: 0;
      row-gap: 0;
    }

    &__slide {
      flex: 0 0 360px;
    }
  }
}

@media all and (max-width: 600px) {
  .certeficat-slider {
    padding: 40px 10px 40px 10px;
    overflow: hidden;
    &__inner {
      position: relative;
      padding: 0 30px;
    }

    &__slide {
      flex: 1 0 100%;
    }
  }

  .certeficat-slide {
    &__wrap {
      padding: 60px 20px 20px 20px;
    }

    &__pretitle {
      margin-bottom: 12px;
      font-size: 16px;
    }

    &__title {
      font-size: 72px;
    }

    &__subtitle {
      margin-bottom: 12px;
      font-size: 22px;
    }

    &__label {
      margin-bottom: 4px;
      align-self: start;
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 16px;
      line-height: 145%;
      color: #2d2d2d;
    }

    &__discount {
      font-size: 20px;
    }

    &__price {
      margin: 12px 0;
      font-size: 24px;
      &:after {
        content: " РУБ";
      }
    }
    &__link {
      padding: 12px 16px;
      display: flex;
      align-items: center;
    }
  }
}
